import { graphql, useStaticQuery } from "gatsby"

export default function useSiteMetadata() {
  const data = useStaticQuery(graphql`
    query getMetadata {
      site {
        siteMetadata {
          title
          description
          tagline
          infoData {
            contact {
              email
              linkedin
            }
            belowcontact
            cta
            description
            background_color
          }
        }
      }
    }
  `)
  return data.site.siteMetadata
}