import React from "react"
import { Link } from "gatsby"
import headerStyles from "../styles/components/header.module.scss"
import { navigate } from '@gatsbyjs/reach-router'

export default function Header(props) {

  function renderBackButton(props) {
    if ( props.page !== 'contact' ) {
      return (
       <Link to='/contact/'
        activeClassName={headerStyles.navItemActive}
        >
        {props.page === 'contact'
          ? "close"
          : "contact"}
        </Link>
      )
    } else {
      return (
        <button class='button-link' activeClassName={headerStyles.navItemActive} onClick={() => {
          navigate(-1);
        }}>
          back
        </button>
      )
    }
  }
  return (
    <header
      className={`${headerStyles.header} ${props.page === 'contact' &&
        headerStyles.info_page}`}
    >
      <nav
        className={headerStyles.header__nav}
        role="navigation"
        aria-label="main navigation"
      >
        <div>
          <h1>
          <Link to="/">
            {props.title}
          </Link>
          </h1>
          <span>{props.tagline}</span>
        </div>
        <div className={headerStyles.bottom_contact}>
          <h1>
            {renderBackButton(props)}
          </h1>
        </div>
      </nav>
    </header>
  )
}